import React from 'react';
import { Button, Divider, Form, Grid, Segment } from 'semantic-ui-react';
import zoo1 from '../../files/imgesauth/logo2.svg';
import zoo2 from '../../files/imges/0.png'
import { Breadcrumb } from 'semantic-ui-react'

export const Trial= () => (
  <Segment placeholder>
    <Grid columns={2} relaxed='very' stackable>
      <Grid.Column>     
        <Form verticalAlign = "left"><br/>
        <Segment textAlign='center'>
        <img position = "right" width={150} height={130} className="right" src={zoo1} alt="right" />
        </Segment><br/>
        
          <Form.Input icon='user' iconPosition='left' label='Username' placeholder='Your Email Adress' />
          <Form.Input icon='lock' iconPosition='left' label='Password' placeholder='Your PassWord' type='passwordf' />
         
          <br/><br/>
          <Button content='Start Your Free 30-Day Trial' secondary />
          <Divider horizontal>Or</Divider>
         
          <Button content='Login' primary />
          <br/>
          <ul> Already have an account? <a href = ""> Sign In   <Breadcrumb.Divider icon='right arrow' /> </a></ul>          
        
        </Form>
      </Grid.Column>
      
      <Form verticalAlign = "center"><br/>
        <Grid.Column verticalAlign='Center'>
        
          <img src={zoo2} width="200" height="100" className="center" alt="center" />         

        </Grid.Column>
      </Form>
    </Grid>
    
    <Divider vertical></Divider>
    
  </Segment>
)

